import Authorization from "./user/auth.service";

const HomeService = {
    getSearchInfo() {
        try {
            return Promise.all([this.getHotels(),this.getDestinations()]).then(list => {
                return {
                    hotels: list[0],
                    destinations: list[1],
                }
            })
        } catch (e) {
            console.log(e)
        }
    }, 
    async getHotels(pole_id=undefined) {
        try {
            const {status, data} = (pole_id == undefined) ? await Authorization.checkLogin(`/hotels`) :
                await Authorization.checkLogin(`/hotels/by-pole/${pole_id}`)
            
            if(status == 200) {
                const hotels = []
                for(let h of data) {
                    hotels.push({
                        text: h.alias,
                        value: h.id,
                    })
                }
                return hotels
            }
        } catch (e) {
            console.log(e)
        }
    },
    async getDestinations() {
        try {
            const {status, data} = await Authorization.checkLogin(`/poles`)
            
            if(status == 200) {
                const destinations = []
                for(let d of data) {
                    destinations.push({
                        text: d.alias,
                        value: d.id,
                    })
                }
                return destinations
            }
        } catch (e) {
            console.log(e)
        }
    }   
}

export default HomeService